
import { defineComponent } from "vue";
import draggable from "vuedraggable";
import CityPlate from "@/components/settings/CityPlate.vue";
import { IOnFocus } from "@/types/IApi";
import debounce from "lodash/debounce";

interface IData {
  isFocus: boolean;
  enabled: boolean;
  dragging: boolean;
  newCityName: string;
}

export default defineComponent({
  name: "SettingBlock",
  emits: [
    "deleteCity",
    "replaceCity",
    "addFirstCity",
    "findNewCities",
    "addCityAndCountry",
  ],
  components: {
    CityPlate,
    draggable,
  },
  props: {
    cities: {
      type: Array,
      required: true,
    },
    errorMessage: {
      type: String,
      required: false,
      default: "",
    },
    citiesOptions: {
      type: Array,
      required: true,
    },
  },
  data(): IData {
    return {
      enabled: true,
      dragging: false,
      isFocus: false,
      newCityName: "",
    };
  },
  methods: {
    findNewCities(): void {
      if (this.$data.newCityName) {
        this.$emit("findNewCities", this.$data.newCityName);
      }
    },
    debounceFindNewCities: debounce(function () {
      this.findNewCities();
    }, 500),
    addCityAndCountry(id: string): void {
      this.$emit("addCityAndCountry", id);
      this.$data.newCityName = "";
    },
    addFirstCity(): void {
      if (this.$data.newCityName) {
        this.$emit("addFirstCity", this.$data.newCityName);
        this.$data.newCityName = "";
      }
    },
    onDeleteCity(cityId: string): void {
      this.$emit("deleteCity", cityId);
    },
    onEndDrag(): void {
      this.$data.dragging = false;
      this.$emit("replaceCity");
    },
    onFocus(e: IOnFocus<HTMLInputElement>): void {
      e.target?.select();
      this.$data.isFocus = true;
    },
  },
  computed: {
    inputStyle(): string {
      return this.$props.errorMessage ? "input-error input" : "input";
    },
    labelText(): string {
      if (this.$data.isFocus) {
        return "Add city:";
      }
      return "Add city: " + this.$props.errorMessage;
    },
  },
});
