
import { defineComponent } from "vue";
export default defineComponent({
  name: "CityPlate",
  emits: ["deleteCity"],
  props: {
    cityId: {
      type: String,
      required: true,
    },
  },
  methods: {
    onClick(): void {
      this.$emit("deleteCity", this.$props.cityId);
    },
  },
});
