
import { fetchIcon } from "@/api/fetchIcon";
import { defineComponent } from "vue";
import { getCelsiusDegreeFromKelvin } from "@/helpers/getCelsiusDegreeFromKelvin";

export default defineComponent({
  name: "WeatherBlock",
  props: {
    cityName: {
      type: String,
      required: true,
    },
    countryCode: {
      type: String,
      required: true,
    },
    temperatureMain: {
      type: Number,
      required: true,
    },
    temperatureFeel: {
      type: Number,
      required: true,
    },
    iconCode: {
      type: String,
      required: true,
    },
    sky: {
      type: String,
      required: true,
    },
    humidity: {
      type: Number,
      required: true,
    },
    pressure: {
      type: Number,
      required: true,
    },
    visibility: {
      type: Number,
      required: true,
    },
    windSpeed: {
      type: Number,
      required: true,
    },
  },
  computed: {
    fullName(): string {
      const { cityName, countryCode } = this.$props;
      return cityName + ", " + countryCode;
    },
    mainTemperature(): string {
      return getCelsiusDegreeFromKelvin(this.$props.temperatureMain);
    },
    feelTemperature(): string {
      return getCelsiusDegreeFromKelvin(this.$props.temperatureFeel);
    },
    iconURL(): string {
      return fetchIcon(this.$props.iconCode);
    },
    visibilityKM(): string {
      return this.$props.visibility / 1000 + "km";
    },
  },
});
